<template>
  <ion-page>
    <ion-header>
      <ion-toolbar>
        <ion-title>Laporan Kehadiran</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>
    <ion-content class="ion-padding">
      <h1>Filter Role</h1>
      <ion-item>
        <ion-label>Role</ion-label>
        <ion-select v-model="selectedRole" @ionChange="filterRecords">
          <ion-select-option v-for="role in roles" :key="role" :value="role">{{
            role
          }}</ion-select-option>
        </ion-select>
      </ion-item>

      <h1>Filter Tanggal</h1>
      <ion-item>
        <ion-label>Dari</ion-label>
        <ion-button
          style="color: white"
          expand="block"
          @click="openStartDatePicker"
          >{{ startDate ? formatDate(startDate) : "Pilih Tanggal" }}</ion-button
        >
      </ion-item>
      <ion-item>
        <ion-label>Sampai</ion-label>
        <ion-button
          style="color: white"
          expand="block"
          @click="openEndDatePicker"
          >{{ endDate ? formatDate(endDate) : "Pilih Tanggal" }}</ion-button
        >
      </ion-item>

      <ion-modal
        :is-open="isStartDatePickerOpen"
        @ion-modal-did-dismiss="closeStartDatePicker"
      >
        <ion-content>
          <div class="date-picker-modal">
            <ion-datetime
              display-format="YYYY-MM-DD"
              picker-format="YYYY-MM-DD"
              v-model="startDate"
              @ionChange="filterRecords"
            ></ion-datetime>
            <ion-button expand="block" @click="closeStartDatePicker"
              >Tutup</ion-button
            >
          </div>
        </ion-content>
      </ion-modal>

      <ion-modal
        :is-open="isEndDatePickerOpen"
        @ion-modal-did-dismiss="closeEndDatePicker"
      >
        <ion-content>
          <div class="date-picker-modal">
            <ion-datetime
              display-format="YYYY-MM-DD"
              picker-format="YYYY-MM-DD"
              v-model="endDate"
              @ionChange="filterRecords"
            ></ion-datetime>
            <ion-button expand="block" @click="closeEndDatePicker"
              >Tutup</ion-button
            >
          </div>
        </ion-content>
      </ion-modal>

      <h1>Filter Keterlambatan</h1>
      <ion-item>
        <ion-label>Telat</ion-label>
        <ion-toggle
          v-model="isLateFilter"
          @ionChange="filterRecords"
        ></ion-toggle>
      </ion-item>

      <h1>Pencarian Nama Lengkap</h1>
      <ion-item>
        <ion-label>Nama Lengkap</ion-label>
        <ion-input
          v-model="searchQuery"
          placeholder="Cari Nama Lengkap"
          @ionInput="filterRecords"
        ></ion-input>
      </ion-item>

      <h1>Laporan Absensi</h1>
      <ion-grid class="table">
        <ion-row class="table-header">
          <ion-col>Nama Lengkap</ion-col>
          <ion-col>Role</ion-col>
          <ion-col>Tanggal</ion-col>
          <ion-col>Masuk</ion-col>
          <ion-col>Keluar</ion-col>
          <ion-col>Keterangan Telat</ion-col>
        </ion-row>
        <ion-row v-if="filteredAttendanceRecords.length === 0">
          <ion-col colspan="6">Tidak ada data absensi</ion-col>
        </ion-row>
        <ion-row
          v-for="record in paginatedAttendanceRecords"
          :key="record.id"
          class="table-row"
        >
          <ion-col>{{ record.fullName }}</ion-col>
          <ion-col>{{ record.role }}</ion-col>
          <ion-col>{{ record.date }}</ion-col>
          <ion-col>
            <ion-badge :color="isLate(record.masuk) ? 'danger' : 'success'">
              {{ record.masuk ? record.masuk : "Belum absen" }}
            </ion-badge>
          </ion-col>
          <ion-col>
            {{ record.keluar ? record.keluar : "Belum absen" }}
          </ion-col>
          <ion-col>
            {{ getLateTime(record.masuk) }}
          </ion-col>
        </ion-row>
      </ion-grid>
      <ion-button
        @click="loadMoreAttendanceRecords"
        v-if="showMoreAttendanceRecords"
        color="primary"
        >Load More Attendance Records</ion-button
      >
      <ion-button @click="exportFilteredAttendanceToExcel" color="success"
        >Export Laporan Absensi ke Excel</ion-button
      >

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="toast.isOpen = false"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import {
  getFirestore,
  collection,
  getDocs,
  query,
  orderBy,
} from "firebase/firestore";
import {
  IonBackButton,
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonGrid,
  IonRow,
  IonCol,
  IonBadge,
  IonButton,
  IonToast,
  IonItem,
  IonLabel,
  IonSelect,
  IonSelectOption,
  IonDatetime,
  IonModal,
  IonToggle,
  IonInput,
} from "@ionic/vue";
import { firebaseApp } from "../firebase.js";
import * as XLSX from "xlsx";

const db = getFirestore(firebaseApp);
const attendanceRecords = ref([]);
const roles = ref([]);
const selectedRole = ref("");
const startDate = ref("");
const endDate = ref("");
const isLateFilter = ref(false);
const searchQuery = ref("");
const toast = ref({
  isOpen: false,
  message: "",
  duration: 2000,
  color: "success",
});
const attendancePage = ref(1);
const attendancePerPage = 10;

const isStartDatePickerOpen = ref(false);
const isEndDatePickerOpen = ref(false);

const openStartDatePicker = () => {
  isStartDatePickerOpen.value = true;
};

const closeStartDatePicker = () => {
  isStartDatePickerOpen.value = false;
};

const openEndDatePicker = () => {
  isEndDatePickerOpen.value = true;
};

const closeEndDatePicker = () => {
  isEndDatePickerOpen.value = false;
};

const fetchRoles = async () => {
  const usersSnapshot = await getDocs(collection(db, "users"));
  const rolesSet = new Set();
  usersSnapshot.forEach((doc) => {
    const data = doc.data();
    if (data.role) {
      rolesSet.add(data.role);
    }
  });
  roles.value = Array.from(rolesSet);
};

const fetchAttendanceRecords = async () => {
  console.log("Fetching attendance records...");
  try {
    const usersSnapshot = await getDocs(collection(db, "users"));
    const usersList = usersSnapshot.docs.map((doc) => ({
      id: doc.id,
      ...doc.data(),
    }));

    let records = [];
    for (const user of usersList) {
      const recordsRef = collection(db, "attendance", user.id, "records");
      const recordsSnapshot = await getDocs(
        query(recordsRef, orderBy("masuk", "desc"))
      );

      records = records.concat(
        recordsSnapshot.docs.map((doc) => {
          const data = doc.data();
          return {
            id: doc.id,
            userId: user.id,
            fullName: user.fullName,
            role: user.role,
            date: doc.id,
            masuk: data.masuk
              ? data.masuk.toDate().toLocaleString()
              : "Belum absen",
            keluar: data.keluar
              ? data.keluar.toDate().toLocaleString()
              : "Belum absen",
            lateTime: getLateTime(data.masuk ? data.masuk.toDate() : null),
          };
        })
      );
    }

    attendanceRecords.value = records;
    console.log("Attendance records fetched:", attendanceRecords.value);
  } catch (error) {
    console.error("Error fetching attendance records:", error);
    showToast("Error fetching attendance records.", "danger");
  }
};

const filteredAttendanceRecords = computed(() => {
  return attendanceRecords.value.filter((record) => {
    const recordDate = new Date(record.date);
    const start = startDate.value
      ? new Date(startDate.value)
      : new Date("2000-01-01");
    const end = endDate.value ? new Date(endDate.value) : new Date();
    const isLateRecord = isLate(record.masuk);
    const matchesName = record.fullName
      .toLowerCase()
      .includes(searchQuery.value.toLowerCase());
    return (
      (!selectedRole.value || record.role === selectedRole.value) &&
      recordDate >= start &&
      recordDate <= end &&
      (!isLateFilter.value || isLateRecord) &&
      matchesName
    );
  });
});

const paginatedAttendanceRecords = computed(() => {
  return filteredAttendanceRecords.value.slice(
    0,
    attendancePage.value * attendancePerPage
  );
});

const showMoreAttendanceRecords = computed(() => {
  return (
    attendancePage.value * attendancePerPage <
    filteredAttendanceRecords.value.length
  );
});

const loadMoreAttendanceRecords = () => {
  attendancePage.value++;
};

const isLate = (timestamp) => {
  if (!timestamp) return false;
  const time = new Date(timestamp);
  const eightAM = new Date(time);
  eightAM.setHours(8, 0, 0, 0); // 8 AM
  return time > eightAM;
};

const getLateTime = (timestamp) => {
  if (!timestamp) return "";
  const time = new Date(timestamp);
  const eightAM = new Date(time);
  eightAM.setHours(8, 0, 0, 0); // 8 AM
  if (time > eightAM) {
    const diff = Math.floor((time - eightAM) / 60000); // difference in minutes
    const hours = Math.floor(diff / 60);
    const minutes = diff % 60;
    return `${hours} jam ${minutes} menit`;
  }
  return "Tepat Waktu";
};

const exportFilteredAttendanceToExcel = () => {
  const worksheet = XLSX.utils.json_to_sheet(filteredAttendanceRecords.value);
  const workbook = XLSX.utils.book_new();
  XLSX.utils.book_append_sheet(workbook, worksheet, "Laporan Absensi");
  XLSX.writeFile(workbook, "laporan_absensi.xlsx");
};

const showToast = (message, color) => {
  toast.value = {
    isOpen: true,
    message,
    duration: 2000,
    color,
  };
};

const formatDate = (date) => {
  const d = new Date(date);
  const month = `${d.getMonth() + 1}`.padStart(2, "0");
  const day = `${d.getDate()}`.padStart(2, "0");
  const year = d.getFullYear();
  return [year, month, day].join("-");
};

const filterRecords = () => {
  attendancePage.value = 1;
};

onMounted(() => {
  fetchRoles();
  fetchAttendanceRecords();
});
</script>

<style>
.table {
  border-collapse: collapse;
  width: 100%;
}

.table-header {
  background-color: #f2f2f2;
  font-weight: bold;
}

.table-row {
  border-bottom: 1px solid #ddd;
}

.table-row ion-col {
  border: 1px solid #ddd;
  padding: 8px;
}

.table-row:hover {
  background-color: #f1f1f1;
}

.table-row ion-badge {
  margin-top: 4px;
}

.date-picker-modal {
  padding: 20px;
  text-align: center;
}
</style>
